
.notes__main-content{
    display: flex;
    flex-direction: column;
    height: 100%;
}

.notes__appbar{
    align-items: center;
    background-color: $primary;
    color: white;
    display: flex;
    justify-content: space-between;
    padding: 10px 20px 10px 20px;
}

.notes__content{
    display: flex;
    flex-direction: column;
    height: 100%;
    padding: 20px;
    // background-color: red;
}

.notes__title-input , .notes__text-area{
    border: none;
    
    &:focus{
        outline: none;
    }
}

.notes__title-input{
    color: $dark-grey;
    font-size: 25px;
    font-weight: 700;
    margin-bottom: 10px;
}

.notes__text-area{
    border: none;
    color: $dark-grey;
    font-size: 20px;
    flex: 1 1 auto;
    resize: none;
}

.notes__image img{
    // box-shadow: 5px 5px $dark-grey;
    border-radius: 10px;
    height: 150px;
    box-shadow: 0 10px 10px 0 rgba(0, 0, 0, 0.3);

}


